<template>
<div class="main">
    <el-carousel indicator-position="outside" class="banner">
      <el-carousel-item v-for="item in bannersrc" :key="item">
          <img :src="item.src" alt="" id="banner">
      </el-carousel-item>
    </el-carousel>
    <el-card class="box-card">
        <div>
          <center>
              <p >实验室简介</p>
              实验室包括一个教授
          </center>
        </div>
    </el-card>
    <div>
        <p>实验室教师</p>
    </div>
    <el-table
      :data="tableT"
      style="width: 100%">
      </el-table-column>
      <el-table-column
        prop="name"
        label="Name"
        width="180">
      </el-table-column>
      <el-table-column
        prop="Tel"
        label="tel">
      </el-table-column>
      <el-table-column
        prop="Research"
        label="research">
      </el-table-column>

    </el-table>
    <div>
        <p>实验室研究生</p>
    </div>
  <el-table
    :data="tableS.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="Date"
      prop="date">
    </el-table-column>

    <el-table-column
      label="Name"
      prop="name">
    </el-table-column>

    <el-table-column
      label="Github"
      prop="github">
    </el-table-column>

    <el-table-column
      label="Gender"
      prop="gender">
    </el-table-column>

    <el-table-column
      label="Research"
      prop="research">
    </el-table-column>

    <el-table-column
      label="Blog"
      prop="blog">
    </el-table-column>

    <el-table-column
      label="Tel"
      prop="tel">
    </el-table-column>
    <el-table-column
        prop="Cv"
        label="Cv">
      </el-table-column>
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入关键字搜索"/>
      </template>
  
    </el-table-column>
       
  </el-table>
 
</div>
</template>

<script>
  export default {
    name:"Home",
    data() {
      return {
        bannersrc:[
            {src:require("@/assets/banner1.jpg")},
            {src:require("@/assets/banner2.jpg")},
            {src:require("@/assets/banner3.jpg")},
        ],
        tableS: [{
          date: '2018',
          name: '张超',
          github: 'http',
          blog:"http"
        }, {
          date: '2018',
          name: '刘智鹏',
          github: 'http',
          blog:"http"
        }, {
          date: '2018',
          name: '王小虎',
          github: 'http',
          blog:"http"
        }, {
          date: '2019',
          name: '宋子盈',
          github: 'http',
          blog:"http",
          research:"机器视觉、联邦学习",
          cv:""
        }],
        search: ''
      }
    },

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main{
    top:258px;
    position:absolute;
    width: 1200px;
    height: 800px;
    margin: auto;

    left: 0;
    bottom: 0;
    right: 0;


}


</style>



