import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/Home'
import Team from '@/page/Team'
import Research from '@/page/Research'
import Contact from '@/page/Contact'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: Home
    }, {
        path: '/team',
        name: 'Team',
        component: Team
    }, {
        path: '/research',
        name: 'Research',
        component: Research
    }, {
        path: '/contact',
        name: 'Contact',
        component: Contact
    }]
})


