<template>
<div class="main">
    <div class="logo">
        <img src="@/assets/logo.jpg" alt="logo" id="logo">
    </div>
    <div class="home">
        <span style="cursor:pointer"  @click="gohome">Home</span>
    </div>
    <div class="team">
        <span style="cursor:pointer"  @click="goteam">Team</span>
    </div>
    <div class="research">
        <span style="cursor:pointer"  @click="goresearch">Research</span>  
    </div>
    <div class="contact">
        <span style="cursor:pointer"  @click="gocontact">Contact</span>  
    </div>
</div>
</template>

<script>
export default {
  name: 'Home',
  data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
  },
  methods:{
      gohome(){
  		    this.$router.push('/')
  	  },
      goteam(){
  		    this.$router.push('/team')
  	  },
      goresearch(){
  		    this.$router.push('/research')
  	  },
      gocontact(){
  		    this.$router.push('/contact')
  	  }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


.main{
    width:100%;
    height:100px;
    background-color:#000;
    position:absolute;
    margin-left:-8px;
    top:0;
    margin: auto;
    left: 0;
    right: 0;
}
.logo{
    width:150px;
    height:100px;
    background-color:red;
    left:15%;
    overflow:hidden;

    position:relative;
    
}
#logo{
    width:150px;
    height:100px;
}
.home{
    width:150px;
    height:100px;
    color:white;
    position:relative;
    top:-100px;
    left:30%;
    line-height:100px;
    font-size:30px;
    font:#255;

}
.team{
    width:150px;
    height:100px;
    color:white;
    position:relative;
    top:-200px;
    left:45%;
    line-height:100px;
    font-size:30px;
    font:#255;
}
.research{
    width:150px;
    height:100px;
    color:white;
    position:relative;

    top:-300px;
    left:60%;
    line-height:100px;
    font-size:30px;
    font:#255;
}
.contact{
    width:150px;
    height:100px;
    color:white;
    position:relative;

    top:-400px;
    left:75%;
    line-height:100px;
    font-size:30px;
    font:#255;
}
</style>



